import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { cancelBubble } from '@cetus/utils'
import {
  Box,
  HStack,
  LayoutProps,
  Popover,
  PopoverBody,
  PopoverBodyProps,
  PopoverContent,
  PopoverTrigger,
  Portal,
  Skeleton,
  StackProps,
  Text,
  TextProps,
  Tooltip,
  VStack
} from '@chakra-ui/react'
import React from 'react'

export type HTextLabelBoxProps = {
  label: string | JSX.Element
  value: string | JSX.Element
  subLabel?: string | JSX.Element
  subValue?: string | JSX.Element
  leftTooltip?: string | JSX.Element
  rightValueTip?: string | JSX.Element
  isLoading?: boolean
  skeletonStyle?: HTextLabelSkeletonProps
  wrapStyle?: StackProps
  labelStyle?: TextProps
  valueStyle?: TextProps
  subLabelStyle?: TextProps
  subValueStyle?: TextProps
  showTooltip?: boolean
  rightValueTipWrapStyle?: PopoverBodyProps
}

export default function HTextLabelBox(props: HTextLabelBoxProps) {
  const {
    label,
    value,
    subLabel,
    subValue,
    leftTooltip,
    rightValueTip,
    isLoading,
    skeletonStyle = {},
    wrapStyle = {},
    labelStyle = {},
    valueStyle = {},
    subLabelStyle = {},
    subValueStyle = {},
    showTooltip = true,
    rightValueTipWrapStyle = {}
  } = props

  const { isApp } = useWindowWidth()

  return isLoading ? (
    <HTextLabelSkeleton {...skeletonStyle} {...props} />
  ) : (
    <HStack w="100%" justifyContent="space-between" alignItems="center" {...wrapStyle}>
      <VStack alignItems="start" gap="1px">
        {leftTooltip ? (
          <Tooltip
            label={
              <Text fontSize="12px" lineHeight="20px">
                {leftTooltip}
              </Text>
            }
            aria-label="Yield"
            ml="12px"
            mr="12px"
          >
            <button onClick={e => cancelBubble(e)}>
              <Text cursor="help" textDecoration={labelStyle?.textDecoration || 'underline dotted'} textUnderlineOffset="3px" fontSize="12px">
                {label}
              </Text>
            </button>
          </Tooltip>
        ) : (
          <Text color="text_paragraph" fontSize="12px" {...labelStyle}>
            {label}
          </Text>
        )}
        {subLabel && (
          <Text fontSize="12px" {...subLabelStyle}>
            {subLabel}
          </Text>
        )}
      </VStack>

      <VStack alignItems="end" gap="1px">
        {/* {rightValueTip ? (
          <Tooltip
            label={
              <Text fontSize="12px" lineHeight="20px">
                {rightValueTip}
              </Text>
            }
            aria-label="Yield"
            ml="12px"
            mr="12px"
          >
            <button onClick={e => cancelBubble(e)}>
              <Text
                cursor="help"
                maxW="220px"
                color="text_caption"
                textDecoration="underline dotted"
                textUnderlineOffset="3px"
                textAlign="start"
                fontSize="12px"
                {...valueStyle}
              >
                {value}
              </Text>
            </button>
          </Tooltip> */}
        {rightValueTip ? (
          <Popover isLazy trigger={isApp ? 'click' : 'hover'} autoFocus={false} returnFocusOnClose={false} gutter={4}>
            <PopoverTrigger>
              <Box as="button">
                <Text
                  cursor="help"
                  maxW="220px"
                  color="text_caption"
                  textDecoration="underline dotted"
                  textUnderlineOffset="3px"
                  textAlign="start"
                  fontSize="12px"
                  {...valueStyle}
                >
                  {value}
                </Text>
              </Box>
            </PopoverTrigger>
            {showTooltip && (
              <Portal>
                <PopoverContent w="fit-content">
                  <PopoverBody fontSize="12px" w="fit-content" {...rightValueTipWrapStyle}>
                    {rightValueTip}
                  </PopoverBody>
                </PopoverContent>
              </Portal>
            )}
          </Popover>
        ) : (
          <Text textAlign="right" maxW="220px" color="text_caption" fontSize="12px" {...valueStyle}>
            {value}
          </Text>
        )}
        {subValue && (
          <Text maxW="220px" fontSize="12px" {...subValueStyle}>
            {subValue}
          </Text>
        )}
      </VStack>
    </HStack>
  )
}

type HTextLabelSkeleton = {
  valueW?: LayoutProps['w']
  subValueW?: LayoutProps['w']
  valueH?: LayoutProps['h']
  subValueH?: LayoutProps['h']
}

type HTextLabelSkeletonProps = HTextLabelSkeleton & Partial<HTextLabelBoxProps>

function HTextLabelSkeleton(props: HTextLabelSkeletonProps) {
  const {
    valueW = '60px',
    subValueW = '80px',
    valueH = '20px',
    subValueH = '20px',
    label,
    value,
    subLabel,
    subValue,
    leftTooltip,
    rightValueTip,
    isLoading,
    skeletonStyle = {},
    wrapStyle = {},
    labelStyle = {},
    valueStyle = {},
    subLabelStyle = {},
    subValueStyle = {}
  } = props
  return (
    <HStack w="100%" justifyContent="space-between" alignItems="center" {...wrapStyle}>
      <VStack alignItems="start" gap="1px">
        {leftTooltip ? (
          <Tooltip label={<Text>{leftTooltip}</Text>} aria-label="Yield">
            <button onClick={e => cancelBubble(e)}>
              <Text textDecoration="underline dotted" textUnderlineOffset="3px" fontSize="12px" {...labelStyle}>
                {label}
              </Text>
            </button>
          </Tooltip>
        ) : (
          <Text color="text_paragraph" fontSize="12px" {...labelStyle}>
            {label}
          </Text>
        )}
        {subLabel && (
          <Text fontSize="12px" {...subLabelStyle}>
            {subLabel}
          </Text>
        )}
      </VStack>

      <VStack alignItems="end" gap="1px">
        {(typeof value === 'string' || React.isValidElement(value)) && <Skeleton w={valueW} h={valueH} />}
        {subValue && typeof subValue === 'string' && <Skeleton w={subValueW} h={subValueH} />}
      </VStack>
    </HStack>
  )
}
